body {
  display: flex;
  margin: 0;
  padding: 0;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial;
  color: darkgrey;
  flex-direction: column;
}

.container {
  margin: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.properties {
  display: flex;
  flex-wrap: wrap;
}

.footer {
  width: 100%;
  background-color: lightcoral;
  position: absolute;
  bottom: 0;
  text-align: center;
  padding: 30px;
  color: white;
}

.card {
  width: 150px;
  margin: 5px;
  border-radius: 20px;
  border: 1px solid #f4f4f4;
  padding: 10px;
  box-shadow: (0 0 0/12%) 0 1px 8px;
}

.card > img {
  width: 130px;
  height: 130px;
}

.nav-bar {
  width: 100%;
  position: sticky;
  background-color: white;
  box-shadow: rgb(0 0 0 / 12%) 0 1px 8px;
  display: flex;
  justify-content: space-between;
}

.logo {
  height: 30px;
  width: 46px;
  background-size: 47px;
  background-image: url("images/sofa-logo.png");
  margin: 20px;
}

.user-container {
  margin: 20px;
}
